/* Contains all the enums */

const fileTypeArray = [
    {
        id: 1,
        commonName: "pdf",
        shortForm: "PDF",
        fullForm: "Portable Document Format"
    },
    {
        id: 2,
        commonName: "xls",
        shortForm: "XLS",
        fullForm: "Microsoft Excel file"
    },
    {
        id: 3,
        commonName: "csv",
        shortForm: "CSV",
        fullForm: "Comma-Separated Values"
    }
];

const getFileTypeById = (id) => {
    return fileTypeArray.find((fileType, index) => {
        return fileType.id == id;
    })
};
const getFileTypeByName = (name) => {
    return fileTypeArray.find((fileType, index) => {
        return fileType.name == name;
    })
};


