import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import Select from 'react-select';
import { yesAndNoList } from 'src/Utils/Enums';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "ExpensesView.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    storeListData: [],
    expensesListData: [],
};


class ExpensesView extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchExpenseDataList();
    }

    fetchExpenseDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/expense/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchExpenseDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        expensesListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchExpenseDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    //default
    componentDidMount() {
        document.title = "Satanlabs - expenses view page";
        this.fetchExpenseDataList();
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>EXPENSES VIEW &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <br />
                <Table striped bordered hover color='success' >
                    <thead>
                        <tr>
                        <th>#</th>
                        <th>dateAndTimeOfPurchase</th>
                        <th>payment</th>
                        <th>items</th>
                        <th>mode</th>
                        <th>storeId</th>
                        <th>locationId</th>
                        <th>extraNotes</th>
                        <th>tags</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.expensesListData.map(
                                expense => 
                                <tr key={"expense_item_id_" + expense.id}>
                                    <td>{expense.id}</td>
                                    <td>{expense.dateAndTimeOfPurchase}</td>

                                    <td>
                                        <tr>
                                            <th>totalAmount</th>
                                            <th>paymentInstrument</th>
                                            <th>bank</th>
                                        </tr>
                                        <tr>
                                            <td>‚Çπ{expense.payment.totalAmount}</td>
                                            <td>{expense.payment.paymentInstrument}</td>
                                            <td>{expense.payment.bank}</td>
                                        </tr>
                                    </td>

                                    <td>
                                        <tr>
                                            <th>#</th>
                                            <th>productId</th>
                                            <th>quantity</th>
                                            <th>amount</th>
                                        </tr>

                                        {expense.items.map(
                                            (item, count) => 
                                            <tr>
                                                <td>{count + 1}</td>
                                                <td>{item.productId}</td>
                                                <td>{item.quantity}</td>
                                                <td>‚Çπ {item.amount}</td>
                                            </tr>
                                        )}
                                    </td>

                                    <td>{expense.mode}</td>
                                    <td>{expense.storeId}</td>
                                    <td>{expense.locationId === null ? 'N/A' : expense.locationId}</td>
                                    <td>{expense.extraNotes}</td>
                                    <td>{expense.tags}</td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default ExpensesView;
